export class Client {

  private _id: string;
  private _name: string;
  private _razao: string;
  private _cnpj: string;
  private _email: string;
  private _fone: string;

  private _info:any;
  private _pedidos:any;

	public get pedidos(): any {
		return this._pedidos;
	}

	public set pedidos(value: any) {
		this._pedidos = value;
	}

	public get info(): any {
		return this._info;
	}

	public set info(value: any) {
		this._info = value;
	}

	public get razao(): string {
		return this._razao;
	}

	public set razao(value: string) {
		this._razao = value;
	}

	public get cnpj(): string {
		return this._cnpj;
	}

	public set cnpj(value: string) {
		this._cnpj = value;
	}

	public get email(): string {
		return this._email;
	}

	public set email(value: string) {
		this._email = value;
	}

	public get fone(): string {
		return this._fone;
	}

	public set fone(value: string) {
		this._fone = value;
	}

  public get id(): string {
    return this._id;
  }

  public set id(value: string) {
    this._id = value;
  }

  public get name(): string {
    return this._name;
  }

  public set name(value: string) {
    this._name = value;
  }

}




