import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-minigraph',
  templateUrl: './minigraph.component.html',
  styleUrls: ['./minigraph.component.scss']
})
export class MinigraphComponent implements OnInit {

  @Input() item: any;

  private fontSize:string = "font1";

  constructor() { }

  ngOnInit() {
    let len = this.item.value.length;
    if (len > 2 && len <= 7){
      this.fontSize = "font2";
    } else if (len > 7){
      this.fontSize = "font3";
    }
  }

}



